import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';

import { Product } from './product';

@Injectable()
export class ApiService {

    private API_URL: String = 'http://localhost:4000/api/product';

    constructor(private http: HttpClient) { }

    // this api will fetch all products
    public fetchProducts(productName?): Observable<Product[]> {
        let url = this.API_URL +'/';
        if(productName){
            url += '?productName='+productName;
        }
        return this.http.get<Product[]>(url)
    }

    // this api will fetch product through productId
    public fetchProductDetails(productId): Observable<Product> {
        return this.http.get<Product>(this.API_URL+'/'+productId)
    }

    // this api will delete product
    public deleteProduct(productId): Observable<Product> {
        return this.http.delete<Product>(this.API_URL+'/'+productId)
    }


}



