import { Component, OnInit } from '@angular/core';
import { ApiService } from '../api.service';
import { Product } from '../product';

@Component({
    selector: 'app-product-list',
    templateUrl: './product-list.component.html',
    styleUrls: ['./product-list.component.css']
})
export class ProductListComponent implements OnInit {

    public products: Product[]
    public pages: Number

    constructor(private apiService: ApiService) {

    }

    ngOnInit() {
        this.fetchProducts();
    }

    public fetchProducts() {
        this.apiService.fetchProducts()
            .subscribe(products => this.products = products)
    }

    public deleteProduct(productId, index) {
        this.apiService.deleteProduct(productId)
            .subscribe(() => this.products.splice(index, 1))
    }

    public searchProduct(filter) {
        this.apiService.fetchProducts(filter.name)
        .subscribe((products) => this.products = products)
    }

}



